<template>
  <div id="jigsaw_box" @click.stop>
    <canvas id="canvas" ref="canvas" :width="w" :height="h"></canvas>
    <div class="refresh" @click="refresh"></div>
    <canvas id="block" ref="block" :width="w" :height="h"></canvas>
    <div class="slider">
      {{sliderText}}
      <div class="drag_block" ref="drag" v-drag></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "jigsaw",
  directives: {
    drag: {
      bind(el, binding, vnode) {
        let _this = vnode.context,
          isTouchStart = false,
          // eslint-disable-next-line
          originX,
          // eslint-disable-next-line
          originY,
          // eslint-disable-next-line
          startTime,
          // eslint-disable-next-line
          endTime;

        el.addEventListener("touchstart", e => {
          originX = e.changedTouches[0].clientX;
          originY = e.changedTouches[0].clientY;
          isTouchStart = true;
          startTime = +new Date();
          endTime = 0;
        });
        el.addEventListener("touchmove", e => {
          e.x = e.changedTouches[0].clientX;
          e.y = e.changedTouches[0].clientY;
          if (!isTouchStart) return false;
          _this.sliderText = "";
          let moveX = e.x - originX;
          // let moveY = e.y - originY;
          if (moveX < 0 || moveX + 38 >= _this.w) return false;
          if (moveX > _this.w - _this.L) {
            moveX = _this.w - _this.L;
          }
          el.style.left = moveX + "px";

          _this.$refs.block.style.left = `calc(3vw + ${moveX}px)`;
        });
        el.addEventListener("touchend", e => {
          isTouchStart = false;
          // startTime = +new Date();
          endTime = 0;
          e.x = e.changedTouches[0].clientX;
          e.y = e.changedTouches[0].clientY;
          let moveX = e.x - originX;
          // let moveY = e.y - originY;
          if (_this.x - 5 < moveX && _this.x + 5 > moveX) {
            el.style.background = "var(--success)";
            _this.success();
          } else {
            el.style.background = "var(--failed)";
            setTimeout(() => {
              _this.refresh();
            }, 500);
          }
        });
      }
    }
  },
  data() {
    return {
      sliderText: "请拖动滑块完成拼图",
      w: 0,
      h: 0,
      PI: Math.PI,
      x: 0,
      y: 0,
      errorTimes: 0,
    };
  },
  props: {
    l: {
      type: Number,
      default: 40
    },
    r: {
      type: Number,
      default: 10
    },
    maxErrorTimes: {
      type: Number,
      default: 5
    }
  },
  watch: {
    errorTimes(newVal) {
      if (newVal >= this.maxErrorTimes) {
        console.log(newVal);
        this.$emit("maxError");
      }
    }
  },
  mounted() {
    this.canvasCtx = this.$refs.canvas.getContext("2d");
    this.blockCtx = this.$refs.block.getContext("2d");
    this.L = this.l * 1 + this.r * 2;
    // if (2 * this.L + 20 > this.h || 2 * this.L + 20 > this.w) {
    //   return console.error("当前设备无法展示滑块");
    // }
    this.initImg();
    this.draw();
  },
  created() {
    console.log("滑块组件");
    this.w = parseFloat(window.innerWidth * 0.74);
    this.h = parseFloat(window.innerWidth * 0.33);
    require(['../../assets/svg/success.svg','../../assets/svg/failed.svg']);
  },
  methods: {
    getRandomNumberByRange(start, end) {
      return Math.round(Math.random() * (end - start) + start);
    },
    createImg(onload) {
      const img = document.createElement("img");
      img.crossOrigin = "Anonymous";
      img.onload = onload;
      img.onerror = () => {
        img.src = this.getRandomImg();
      };
      img.src = this.getRandomImg();
      return img;
    },
    createElement(tagName) {
      return document.createElement(tagName);
    },
    removeClass(tag, className) {
      tag.classList.remove(className);
    },
    clean() {
      let w = this.w;
      let h = this.h;
      this.canvasCtx.clearRect(0, 0, w, h);
      this.blockCtx.clearRect(0, 0, w, h);
      document.getElementById("block").width = w;
    },
    getRandomImg() {
      return require("@/assets/dragImg/" +
        this.getRandomNumberByRange(1, 33) +
        ".png");
    },
    drawDrag(ctx, operation, x, y) {
      let r = this.r,
        l = this.l,
        PI = this.PI;
      ctx.beginPath();
      ctx.moveTo(x, y);
      ctx.lineTo(x + l / 2, y);
      ctx.arc(x + l / 2, y - r + 2, r, 0, 2 * PI);
      ctx.lineTo(x + l / 2, y);
      ctx.lineTo(x + l, y);
      ctx.lineTo(x + l, y + l / 2);
      ctx.arc(x + l + r - 2, y + l / 2, r, 0, 2 * PI);
      ctx.lineTo(x + l, y + l / 2);
      ctx.lineTo(x + l, y + l);
      ctx.lineTo(x, y + l);
      ctx.lineTo(x, y);
      ctx.fillStyle = "#fff";
      ctx[operation]();
      ctx.beginPath();
      ctx.arc(x, y + l / 2, r, 1.5 * PI, 0.5 * PI);
      ctx.globalCompositeOperation = "xor";
      ctx.fill();
    },
    draw() {
      this.x = this.getRandomNumberByRange(this.L + 10, this.w - this.L - 10);
      this.y = this.getRandomNumberByRange(
        this.r * 2 + 10,
        this.h - this.r * 2 - 30
      );
      this.drawDrag(this.canvasCtx, "fill", this.x, this.y);
      this.drawDrag(this.blockCtx, "clip", this.x, this.y);
    },
    initImg() {
      let L = this.L;
      const img = this.createImg(() => {
        this.canvasCtx.drawImage(img, 0, 0, this.w, this.h);
        this.blockCtx.drawImage(img, 0, 0, this.w, this.h);
        const y = this.y - this.r * 2 + 2;
        const ImageData = this.blockCtx.getImageData(this.x, y, L, L);
        this.$refs.block.width = L;
        this.blockCtx.putImageData(ImageData, 0, y);
      });
      this.img = img;
    },
    refresh() {
      this.$refs.drag.style.left = 0;
      this.$refs.drag.style.background = "var(--slider)";
      this.$refs.block.style.left = "3vw";
      this.sliderText = "请拖动滑块完成拼图";
      this.clean();
      this.initImg();
      this.draw();
      this.errorTimes++;
    },
    success() {
      this.$emit("success");
    }
  }
};
</script>

<style lang='less' scoped>
#jigsaw_box {
  width: 80vw;
  height: 55vw;
  margin: 0 auto;
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 10px;
  padding: 5vw 3vw 0;
  box-sizing: border-box;
  .refresh {
    background: url(~@/assets/svg/refresh.svg) no-repeat center center;
    background-size: 100% 100%;
    width: 6vw;
    height: 6vw;
    position: absolute;
    top: 7vw;
    right: 5vw;
  }
  #block {
    position: absolute;
    top: 5vw;
    left: 3vw;
  }
  .slider {
    height: 10vw;
    width: 100%;
    margin: 2.5vw auto 0;
    font-size: 3.75vw;
    color: #c9c9c9;
    line-height: 10vw;
    text-align: center;
    position: relative;
    border-radius: 5vw;
    background-color: #f5f5f5;
    border: 1px solid #e6e6e6;
    box-sizing: border-box;
    --slider: url(~@/assets/svg/slider.svg) no-repeat center center #4ca2fe;
    --success: url(~@/assets/svg/success.svg) no-repeat center center #52ccba;
    --failed: url(~@/assets/svg/failed.svg) no-repeat center center #f57a7a;
    .drag_block {
      position: absolute;
      height: 100%;
      width: 20%;
      top: 0px;
      left: 0;
      background: var(--slider);
      border-radius: calc(~"5vw - 1px");
      background-color: #4ca2fe;
    }
  }
}
</style>
