<template>
  <div class="top">
    <van-field
      v-model="iphone"
      label=""
      placeholder="请输入手机号"
      type="number"
      maxlength="11"
      clearable
      :error-message="errorMessage"
      @input="formatter"
      :rules="[{ required: true, message: '请填写用户名' }]"
    >
      <template v-if="imgShow" #button>
        <img
          class="hook"
          src="../assets/img/SMSimg/hook.png"
          alt=""
        /> </template
    ></van-field>
    <div class="van_overlay">
      <!-- 短信滑块验证的弹窗 -->
      <van-overlay
        :show="showOverlay"
        @click="showJigsaw && (showOverlay = false)"
      >
        <transition name="fade" mode="out-in">
          <jigsaw @success="jigsawSuccess" v-if="showJigsaw"></jigsaw>
        </transition>
      </van-overlay>
    </div>

    <van-field
      v-model="SMScode"
      center
      clearable
      type="number"
      maxlength="6"
      label=""
      placeholder="请输入短信验证码"
    >
      <template #button>
        <van-button size="small" type="default" @click="SMSclick"
          >发送验证码</van-button
        >
      </template>
    </van-field>
    <div class="but">
      <van-button class="button_info" type="info" @click="buttonInfo"
        >手机验证</van-button
      >
    </div>
  </div>
</template>

<script>
import jigsaw from "@/components/jigsaw";
export default {
  name: "",
  components: { jigsaw },

  data() {
    return {
      iphone: "",
      errorMessage: "",
      imgShow: false,
      showOverlay: false,
      showJigsaw: false,
      SMScode: "",
    };
  },

  created() {},
  mounted() {},
  methods: {
    // 手机验证
    buttonInfo() {
      if (this.regularPhone() && this.SMScode.length == 6) {
        this.$router.push({
          path: "/card/InformationEntry",
        });
      } else {
        this.$toast("手机号或者验证码错误！");
      }
    },
    // 发送验证码
    SMSclick() {
      if (this.regularPhone()) {
        (this.showOverlay = true), (this.showJigsaw = true);
      } else {
        this.$toast("手机号格式错误！");
      }
    },
    // 获取短信验证码成功
    onSuccess() {
      (this.showOverlay = false), (this.showJigsaw = false);
    },
    // 滑块组件
    jigsawSuccess() {
      this.showJigsaw = false;
      // this.getCaptcha();
      this.onSuccess();
    },
    // 手机号正则
    regularPhone() {
      let a = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/;
      return a.test(this.iphone);
    },
    // 过滤输入的数字

    formatter() {
      if (this.regularPhone()) {
        this.imgShow = true;
        // this.errorMessage = "";
      } else {
        this.imgShow = false;
        // this.errorMessage = "手机号格式错误";
      }
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='less' scoped>
.top {
  padding: 10vh 10px 10px 10px;
}
/deep/ input {
  //   border-bottom: 1px solid #e2dfdf;
}
.hook {
  width: 20px;
  height: 20px;
}
.van_overlay {
}
.button_info {
  margin-top: 5vh;
  width: 100%;
  border-radius: 18px;
}
</style>
